<script setup lang="ts">
import { ORDER, THEME_COLOR } from "~/constants";
import EventsAPI from "~/services/events";
import useUserStore from "~/stores/user";

const { t } = useI18n();
const color = useColorMode();
const userStore = useUserStore();
const appConfig = useRuntimeConfig().public;
const items = ref<IModelEvent[]>([]);
const loading = ref(false);
const error = ref<string | null>(null);
const hasBaseFilters = appConfig.APP_FLAGS.hasSelectedVideosFilters;
const filters = reactive({
  order: ORDER.asc,
  sportTypes: [],
});
const clearSportTypes = () => filters.sportTypes = [];
const hasFollowing = computed(() => Boolean(Object.keys(userStore.followed).length));

function fetchSelectedVideos() {
  error.value = null;
  loading.value = true;

  // TODO: Implement passing `filters` as request params

  const params = {
    page: 0,
    pageSize: 4,
    order: filters.order,
    sportTypeFilter: filters.sportTypes,
  };

  // This API doesn't support pagination and ordering for now
  EventsAPI.selected
    .load(hasBaseFilters ? params : undefined)
    .then((response) => {
      items.value = response.content;
      loading.value = false;
    })
    .catch((err: Error) => {
      error.value = err.message;
      loading.value = false;
    });
}

onMounted(() => fetchSelectedVideos());
watch(filters, () => fetchSelectedVideos());
useHead({ title: t("labels.selected_videos") });
definePageMeta({ layout: "home", middleware: "auth" });
</script>

<template>
  <div class="mb-12 main-container">
    <BaseList :items="items" :loading="loading" no-content-text="No selected videos found.. Come back later.">
      <template v-if="items.length" #start>
        <h1 class="mb-10 text-heading-1-bold text-main">
          {{ t('labels.selected_videos') }}
        </h1>

        <BaseAlert v-if="error" type="danger" class="my-6" :text="t(`errors.${error}`)" />

        <ListFilters
          v-if="hasBaseFilters"
          v-model:order="filters.order"
          v-model:sport-types="filters.sportTypes"
          has-order-filter
          has-sport-type-filter
          :disabled="loading"
          @clear="clearSportTypes"
        />
      </template>
      <template #item="{ item }">
        <UserCardEvent :item="item" has-bookmark has-badge />
      </template>
      <template #empty>
        <div class="flex items-center mt-2">
          <IconSelectedNotFound :is-dark-theme="color.value === THEME_COLOR.DARK" />
          <div class="ml-10">
            <h1 class="pageTitle text-main">
              {{ t('pages.videos.title_no_items') }}
            </h1>
            <h3 class="text-secondary">
              {{ t('pages.videos.description_no_items') }}
            </h3>
          </div>
        </div>
      </template>
      <template #skeleton>
        <CardEventSkeleton />
      </template>
    </BaseList>
    <div class="my-10 hr" role="separator" />
    <ListEvents
      v-if="hasFollowing"
      :title="t('pages.videos.user_interesting')"
      class="text-2xl text-main"
      has-see-all-link
      hide-on-no-content
      recommended
    />
  </div>
</template>

<style lang="scss">
.pageTitle {
  @apply text-4xl font-bold mb-2;
}
</style>
